.box-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.box-text {
  display: flex;
  justify-content: center;
}
.box {
  display: inline-block;
  border-radius: 16px;
  box-shadow: 6px 12px rgba(0, 0, 0, 0.2);
  /* background-color: rgba(255, 255, 255, 0.5); */
  background-color: lightblue;
  margin-top: 10px;
  width: 50%;
  height: 200px;
  justify-content: center;
  align-items: center;
}
.header {
  display: flex;
  background-color: black;
  align-items: center;
  justify-content: center;
}
.header-text {
  display: flex;
  color: white;
}
.search {
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-block {
  display: block;
  width: 100px;
}
.form-input {
  height: 50px;
  width: 300px;
  display: block;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 16px 16px;
}
.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}
